import '../cart.css';


import React from 'react'
import { Table, Button, Container } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { addCount, minusCount, sortName } from './../store.js';

function Cart() {
    let cart = useSelector(state => state.cart);
    // console.log(cart[0])
    let dispatch = useDispatch();
    let sum = 0
    const total = () => {
        for (let i in cart) {
            sum += cart[i].price * cart[i].amount
        }
    }
    total();

    let zero = cart[0]
    console.log(zero)



    return (
        <>
            <div className="carttop">

                <h5>SHOPPING CART</h5>
                {/* <button onClick={()=>{
        dispatch(increase(1))
     }}>나이+</button>
     <button onClick={()=>{
        dispatch(increase(100))
     }}>나이+</button> */}
            </div>
            <Container className='mt-5'>
                <Table>
                    <thead>
                        <tr>
                            <th>#</th>
                            <th colspan='2'>ITEM NAME</th>
                            <th>PRICE</th>
                            <th>QUANTITY</th>
                            <th>TOTAL</th>
                        </tr>
                    </thead>
                    <tbody>
                        {
                            cart.map((v, i) =>
                                <tr key={i}>
                                    <td>{v.id}</td>
                                    <td>{v.img}</td>
                                    <td>{v.item}</td>
                                    <td>{(v.price).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</td>
                                    <td>
                                        <Button onClick={() =>
                                            dispatch(addCount(v.id))
                                        
                                            }>+</Button>
                                        {v.amount}
                                        <Button onClick={() => dispatch(minusCount(v.id))}>-</Button>
                                    </td>
                                    <td>{(v.price * v.amount).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}
                                    </td>
                                </tr>
                            )
                        }
                    </tbody>
                    <tfoot>
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <th>TOTAL PRICE:</th>
                            <th>{(sum).toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")}</th>
                        </tr>
                    </tfoot>
                </Table>



                <Button variant='dark' className='cartbtn mt-5 mb-5' onClick={() => {
                    dispatch(sortName())
                }}>구매하기</Button>{''}
            </Container>
        </>
    )
}

export default Cart