// export let {changeName,increase}=user.actions

let cart=createSlice({
    name:'cart',
    initialState:[{id:15, img:'', item:'black and white', price:25000, amount:1} ],
    reducers:{
        addCount(state,action){
            let idx=state.findIndex((a)=>{return a.id===action.payload})
            ++state[idx].amount;
            
        },
        minusCount(state,action){
            let idx=state.findIndex((a)=>{return a.id===action.payload})
            if(state[idx].amount > 1){
                --state[idx].amount;
            }else{
                alert('1개 이상 선택해야 합니다.')
            }
            
        },
        addItem(state,action){
            state.push(action.payload)
        },
        sortName(state){
            state.sort((a,b)=>a.item>b.item?1:-1,);
        }
    }
})

export let {addCount, minusCount, addItem, sortName} = cart.actions

export default configureStore({
    reducer:{
        // user:user.reducer,
        cart:cart.reducer
    },
    middleware: getDefaultMiddleware => getDefaultMiddleware({ serializableCheck: false }),
})