import React from 'react';
import {Nav} from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';

const Product = (props) => {
    // let {title,imgUrl,price} = props.gagu;
    let navigate=useNavigate();
    let {gagu}=props;
    // console.log(props)
    const price=gagu.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")



  return (
    // <Col md={3}>
      <Nav.Link onClick={()=>
      navigate('/detail/'+gagu.id)}>
        <img src={process.env.PUBLIC_URL + '/'+ gagu.imgUrl[0]} width="100%" alt="item" />
        <h4>{gagu.title}</h4>
        <p>&#8361;{price}</p>
        </Nav.Link>
    // </Col>
  )
}

export default Product