import '../detail.css';

import React, { useState } from 'react'
import { Col, Container, Row, Button, Nav } from 'react-bootstrap'
import { useNavigate, useParams } from 'react-router-dom';
import { addItem } from './../store.js';
import { useDispatch } from 'react-redux';

import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import Form from 'react-bootstrap/Form';

import { Swiper, SwiperSlide } from 'swiper/react';
import { FreeMode, Navigation, Thumbs, } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/free-mode';
import 'swiper/css/navigation';
import 'swiper/css/thumbs';
import data from '../data'


import TabContent from './TabContent';


const Detail = (props) => {

    let { gagu } = props;
    let { id } = useParams();
    let selproduct = gagu.find((x) => x.id == id);
    const price = selproduct.price.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    let dispatch = useDispatch();
    let navigate = useNavigate();
    let [tab, setTab] = useState(0);
    const [thumbsSwiper, setThumbsSwiper] = useState(null);

    //main 페이지 gagu 데이터는 필터된 채로 오기 때문에 데이터 갯수 부족
    let origin = data;
    const selex = origin.filter((v, i) => {
        return v.id != id
    })
    // const random = Math.round(Math.random()*selex.length);
    // const rando = selex[random]
    // console.log(rando)

    // const random = Math.round(Math.random()*selex.length);
    // console.log(random)
    const relproduct = selex.slice(0, 4)



    return (
        <>

            <Container>
                <div className='detail-index'>
                    <ul className='index'>
                        <li>Home</li>
                        <li>&gt;</li>
                        <li>Products</li>
                        <li>&gt;</li>
                        <li>Living</li>

                    </ul>
                </div>


                <Row>
                    <Col md={5}>
                        <Swiper
                            style={{
                                '--swiper-navigation-color': '#fff',
                                '--swiper-pagination-color': '#fff',
                            }}
                            loop={true}
                            spaceBetween={20}
                            navigation={true}
                            thumbs={{ swiper: thumbsSwiper && !thumbsSwiper.destroyed ? thumbsSwiper : null }}
                            modules={[FreeMode, Navigation, Thumbs]}
                            className="dsld"
                        >
                            <SwiperSlide>
                                <img src={process.env.PUBLIC_URL + `/${selproduct.imgUrl[0]}`}
                                    alt="#" />

                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={process.env.PUBLIC_URL + `/${selproduct.imgUrl[1]}`} alt="#" />
                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={process.env.PUBLIC_URL + `/${selproduct.imgUrl[2]}`} alt="#" />
                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={process.env.PUBLIC_URL + `/${selproduct.imgUrl[3]}`} alt="#" />
                            </SwiperSlide>
                        </Swiper>
                        <Swiper
                            onSwiper={setThumbsSwiper}
                            loop={true}
                            spaceBetween={40}
                            slidesPerView={4}
                            freeMode={true}
                            watchSlidesProgress={true}
                            modules={[FreeMode, Navigation, Thumbs]}
                            className="dtsld"
                        >
                            <SwiperSlide>
                                <img src={process.env.PUBLIC_URL + `/${selproduct.imgUrl[0]}`} alt="#" />
                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={process.env.PUBLIC_URL + `/${selproduct.imgUrl[1]}`} alt="#" />
                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={process.env.PUBLIC_URL + `/${selproduct.imgUrl[2]}`} alt="#" />
                            </SwiperSlide>
                            <SwiperSlide>
                                <img src={process.env.PUBLIC_URL + `/${selproduct.imgUrl[3]}`} alt="#" />
                            </SwiperSlide>
                        </Swiper>

                    </Col>
                    <Col md={2}></Col>
                    <Col md={5} className='textbox'>
                        <h4>{selproduct.title}</h4>
                        <p>{selproduct.content}</p>
                        <h6>&#8361;{price}</h6>
                        <Tabs
                            defaultActiveKey="guide"
                            transition={false}
                            id="noanim-tab-example"
                            className="mb-3 mt-5"
                        >
                            <Tab eventKey="guide" title="SIZE GUIDE">
                                <ul>
                                    <li>Modeled in size S</li>
                                    <li>Shoulder seam to hem measure approx58/147 cm in length</li>
                                    <li>Model Measurements: Height 59/175cm, Waist 26/66cm, Hips 34.5/88cm</li>
                                </ul>
                            </Tab>
                            <Tab eventKey="shipping" title="SHIPPING">
                                상품 당 &#8361;20,000
                            </Tab>
                            <Tab eventKey="return" title="RETURN">
                                통상 7일 이내에 반품, 교환을 할 수 있습니다.<br />
                                (단, 왕복배송비는 구매자 부담)
                            </Tab>
                        </Tabs>

                        <Tabs
                            defaultActiveKey="walnut"
                            id="uncontrolled-tab-example"
                            className="mb-3 mt-5 colorbox"
                        >
                            <Tab eventKey="color" title="NATURAL COLOR">
                                <Form.Control
                                    type="color"
                                    id="exampleColorInput"
                                    defaultValue="#e4c79c"
                                    title="Choose your color"
                                />
                                <Form.Label htmlFor="exampleColorInput">Natural</Form.Label>
                            </Tab>
                            <Tab eventKey="walnut" title="WALNUT COLOR">
                                <Form.Control
                                    type="color"
                                    id="exampleColorInput"
                                    defaultValue="#a3735c"
                                    title="Choose your color"
                                />
                                <Form.Label htmlFor="exampleColorInput">Walnut</Form.Label>
                            </Tab>
                        </Tabs>

                        <Button className="btn mt-5" onClick={() => {
                            dispatch(addItem({ id: selproduct.id, item: selproduct.title, img: <img src={process.env.PUBLIC_URL + `/${selproduct.imgUrl[0]}`} alt="#" width='50px' />, price: selproduct.price, amount: 1 }))
                            alert(`"${selproduct.title}"를 선택하셨습니다. 장바구니로 이동할까요?`)
                            navigate('/cart')

                        }} >Add the Cart</Button>

                        <Button className="btn mt-5 ms-5" onClick={() => {
                            alert("상품의 기본 옵션을 선택해주세요")
                        }}>구매하기</Button>
                    </Col>
                </Row>

                <Nav variant='tabs' defaultActiveKey="link0" className='mt-5 detailtap'>
                    <Nav.Item>
                        <Nav.Link onClick={() => { setTab(0) }} eventKey="link0">Description</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link onClick={() => { setTab(1) }} eventKey="link1">Additional Information</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link onClick={() => { setTab(2) }} eventKey="link2">Reviews</Nav.Link>
                    </Nav.Item>
                </Nav>

                <TabContent tab={tab} />

            </Container>
            <hr />

            <Container className='relative'>
                <h3>Relative Products</h3>
                <p>Por scientie, musica, sport etc, litot Europa usa li sam vocabular. Li lingues differe solmen in li grammatica, li pronunciation e li plu commun vocabules.</p>
                <div className='re mt-5'>
                    {/* <div className='de-re-pro mt-5'>
                        <img src={process.env.PUBLIC_URL + `/${gagu[5].imgUrl}`} width='100%' objectfit='cover' />
                        <h5>{gagu[5].title}</h5>
                        <h6>{gagu[5].price}</h6>
                    </div> */}

                    <ul>
                        {
                            relproduct.map((v, i) => (
                                <li key={i}>
                                    <img src={process.env.PUBLIC_URL + `/${v.imgUrl[0]}`} />
                                </li>

                            ))
                        }
                    </ul>
                </div>
            </Container>

        </>
    )
}


export default Detail